import React from "react";
import ListItem from "./ListItem";

class List extends React.Component {
  render() {
    const datas = [
      {
        id: 1,
        name: "LinkedIn",
        link: "http://linkedin.com",
        icon: "https://cdn-icons-png.flaticon.com/512/3536/3536505.png",
        bgColor: "#ff9580"
      },
      {
        id: 2,
        name: "GitHub",
        link: "https://github.com",
        icon: "https://cdn-icons-png.flaticon.com/512/270/270798.png",
        bgColor: "#f2faa6"
      },
      {
        id: 3,
        name: "Twitter",
        link: "https://twitter.com/",
        icon: "https://cdn-icons-png.flaticon.com/512/4494/4494477.png",
        bgColor: "#b8b3e8"
      }
    ];
    // Edit the code given below and pass data as props to listItem and map over data.
    //const { datas } = this.state;
    return (
      <div className="List">
        {datas.map((item) => {
          return (
            <ListItem
              id={item.id}
              name={item.name}
              link={item.link}
              icon={item.icon}
              bgColor={item.bgColor}
              atext={"image not found"}
            />
          );
        })}
      </div>
    );
  }
}

export default List;
